import VisualComponent from "jx/core/comps/VisualComponent";
export default class StatusItem extends VisualComponent {
    constructor(dataObject) {
        super(dataObject);
        this.controller = this.dataObject.controller;
        this.item = {
            shortcut: true,
            "id": this.data.id,
            "type": "ComboBox",
            "editable": this.data.editable || true,
            "description": this.data.description || "Etat actuel du projet",
            "value": this.data.value || "open",
            "options": "status"
        };
        this.view = this.item;
    }
}
